.root{
    width: 100%;
    height: 100vh;
    background: linear-gradient(rgba(0,0,0,0.8)50%, rgba(0,0,0,0.8)50%);
    z-index: 55;
    position: fixed;
    .img1{
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 70%;
        cursor: pointer;
        border-radius: 10px;
    }
    p{
        font-size: 20px;
        color: #e5e5e5;
        text-align: center;
    }
}

@media only screen and (max-width: 600px){
    .root{
        .img1{
            width: 80%;
        }
    }
}

@media only screen and (max-width: 500px){
    .root{
        .img1{
            width: 85%;
        }
    }
}

@media only screen and (max-width: 400px){
    .root{
        .img1{
            width: 90%;
        }
        p{
            font-size: 16px;
        }
    }
}