$color1: #e5e5e5;
$color2: #185ADB;
$color3: #1B2430;
$color4: #333333;

.miniContainer{
    max-width: 800px;
    margin: 0 auto;
    padding: 50px 25px;
    h1{
        margin-bottom: 0;
        text-align: left;
    }
    p{
        color: $color1;
        font-size: 19px;
        margin-top: 15px;
        margin-bottom: 35px;
    }
    a{
        background-color: $color2;
        border: none;
        outline: none;
        color: $color1;
        padding: 12px 25px;
        border-radius: 10px;
        font-size: 20px;
        cursor: pointer;
        transition: all .3s ease-in-out;
        &:hover{
            background-color: lighten($color: $color2, $amount: 5);
        }
    }
}