$color1: #e5e5e5;
$color2: #185ADB;
$color3: #1B2430;
$color4: #333333;

.miniContainer{
    max-width: 800px;
    margin: 0 auto;
}

#button{
    background-color: #970101c5;
    border: none;
    outline: none;
    color: $color1;
    padding: 11px 25px;
    border-radius: 10px;
    font-size: 20px;
    margin-top: 10px;
    width: 100%;
    cursor: pointer;
    transition: all .3s ease-in-out;
    &:hover{
        background-color: lighten($color: $color2, $amount: 5);
    }
    span{
        padding-right: 5px;
    }
}

.section3{
    padding: 10px 0px !important;
}

.popup{
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: $color1;
    text-align: center;
    border-radius: 10px;
    padding: 25px 20px 30px;
    width: 600px;
    border-left: 2px solid $color2;
    border-top: 2px solid $color2;
    border-bottom: 2px solid rgb(218, 76, 76);
    border-right: 2px solid rgb(218, 76, 76);
    visibility: visible;
    opacity: 1;
    transition: all .5s ease-in-out;
    .row2{
        display: flex;
        align-items: center;
        justify-content: space-between;
    }
    h2{
        color: $color3;
    }
    button{
        flex-basis: 45%;
        font-size: 20px;
        font-weight: bold;
        border-radius: 5px;
        padding: 8px 0;
    }
    .buttons1{
        background-color: rgb(218, 76, 76);
        color: $color1;
    }
    .buttons2{
        background-color: $color2;
        color: $color1;
    }
}

.popupHidden{
    visibility: hidden;
    opacity: 0;
    position: fixed;
    top: 0%;
    left: 50%;
    background-color: $color1;
    text-align: center;
    border-radius: 10px;
    padding: 25px 20px 30px;
    width: 600px;
    transform: translate(-50%, -50%);
    transition: all .5s ease-in-out;
    width: 600px;
    border-left: 2px solid $color2;
    border-top: 2px solid $color2;
    border-bottom: 2px solid rgb(218, 76, 76);
    border-right: 2px solid rgb(218, 76, 76);
    .row2{
        display: flex;
        align-items: center;
        justify-content: space-between;
    }
    h2{
        color: $color3;
    }
    button{
        flex-basis: 45%;
        font-size: 20px;
        font-weight: bold;
        border-radius: 5px;
        padding: 8px 0;
    }
    .buttons1{
        background-color: rgb(218, 76, 76);
        color: $color1;
    }
    .buttons2{
        background-color: $color2;
        color: $color1;
    }
}

.deleted{
    position: fixed;
    top: 3%;
    right: 5%;
    background-color: rgb(218, 76, 76);
    color: $color1;
    font-weight: bold;
    padding: 3px 10px;
    border-radius: 5px;
    transition: all .3s ease-in-out;
}

.deletedHidden{
    position: fixed;
    top: 3%;
    right: -100%;
    background-color: rgb(218, 76, 76);
    color: $color1;
    font-weight: bold;
    padding: 3px 10px;
    border-radius: 5px;
    transition: all .3s ease-in-out;
}

.root{
    margin: 0 auto;
    padding: 25px;
    padding-bottom: 125px;
    .miniContainer{
        h2{
            font-size: 45px;
            color: $color1;
            text-align: center;
            margin-bottom: 25px;
        }
        h4{
            color: $color1;
            font-size: 30px;
            margin-top: 15px;
            margin-bottom: 10px;
        }
        p{
            color: $color1;
            margin-bottom: 25px;
        }
        button{
            display: block;
            margin: 10px 0;
            font-size: 22px;
            font-weight: 600;
            padding: 14px 15px;
            border-radius: 10px;
            width: 100%;
        }
        .button1{
            color: rgb(95, 160, 95);
            display: block;
            margin: 10px 0;
            font-size: 22px;
            font-weight: 600;
            padding: 10.7px 15px;
            border-radius: 10px;
            width: 100%;
            text-align: center;
            background-color: $color1;
        }
        .button2{
            color: rgb(218, 76, 76);
        }
        .profile{
            background-color: $color1;
            padding: 50px 5px;
            border-radius: 10px;
            h1{
                font-size: 35px;
                color: $color2;
                margin-bottom: 0;
            }
        }
    }
}

@media only screen and (max-width: 700px){
    .popup{
        width: 85%;
    }
    .popupHidden{
        width: 85%;
    }
}

@media only screen and (max-width: 525px){
    .root{
        .miniContainer{
            .profile{
                padding: 30px 5px;
                h1{
                    font-size: 25px;
                }
            }
        }
    }
}

@media only screen and (max-width: 400px){
    .root{
        .miniContainer{
            .profile{
                padding: 30px 5px;
                h1{
                    font-size: 21px;
                }
            }
            button{
                font-size: 19px;
            }
        }
    }
}

@media only screen and (max-width: 345px){
    .root{
        .miniContainer{
            .profile{
                padding: 30px 5px;
                h1{
                    font-size: 18px;
                }
            }
        }
    }
}