$color1: #e5e5e5;
$color2: #185ADB;
$color3: #1B2430;
$color4: #333333;


.root, .rootHidden{
    height: 100vh;
    width: 100%;
    margin: 0 auto;
    position: fixed;
    z-index: 99;
    background-color: $color3;
    .main{
        position: absolute;
        top: 40%;
        left: 50%;
        .img{
            background-color: $color1;
            width: 100px;
            height: 100px;
            display: flex;
            align-items: center;
            justify-content: center;
            border-radius: 30px;    
            transform: translate(-50%, -50%);
            img{
                width: 75px;
                height: 75px;
            }
        }
        h1{
            position: absolute;
            top: 70%;
            left: 0%;
            transform: translate(-50%, -50%);
            font-size: 18px;
            color: $color1;
            font-weight: 600;
            width: 200px;
            display: block;
        }
        .loading{
            left: 50%;
            transform: translateX(-25%);
            color: $color1;
            font-size: 11px;
        }
    }
    .pi{
        position: absolute;
        bottom: 11%;
        left: 50%;
        transform: translateX(-50%);
        display: block;
        font-size: 14px;
        color: $color1;
        span{
            color: $color2;
        }
    }
}

.root{
    visibility: visible;
    opacity: 1;
    transition: all .5s ease-in-out;
}

.rootHidden{
    visibility: hidden;
    opacity: 0;
    transition: all .5s ease-in-out;
}