$color1: #e5e5e5;
$color2: #185ADB;
$color3: #1B2430;
$color4: #333333;

.root{
    max-width: 800px;
    margin: 0 auto;
    padding: 50px 25px 75px;
    h1{
        margin-bottom: 0;
        text-align: left;
    }
    h4{
        color: $color1;
        font-size: 29px;
        margin-top: 15px;
        margin-bottom: 35px;
        span{
            color: $color2;
        }
    }
    h3{
        color: $color1;
        font-size: 29px;
        margin-top: 15px;
        margin-bottom: 35px;
    }
    .block{
        background-color: $color1;
        color: $color3;
        border-radius: 10px;
        padding: 25px;
        h2{
            font-size: 29px;
        }
        .a{
            text-decoration: none;
            color: $color3;
            display: block;
            span{
                color: $color2;
            }
        }
        div{
            display: flex;
            align-items: center;
            margin-top: 15px;
            a{
                text-decoration: none;
                color: $color1;
                text-align: center;
                display: block;
                flex-basis: 20%;
                margin-right: 10px;
                padding: 8px 0;
                border-radius: 10px;
                transition: all .3s ease-in-out;
                &:nth-child(1){
                    background-color: #185ADB;
                    &:hover{
                        box-shadow: 0 0 10px 1px #185ADB;
                    }
                }
                &:nth-child(2){
                    background-color: rgb(95, 160, 95);
                    &:hover{
                        box-shadow: 0 0 10px 1px rgb(95, 160, 95);
                    }
                }
            }
        }
    }
}

@media only screen and (max-width: 625px){
    .root{
        .block{
            div{
                a{
                    flex-basis: 40%;
                }
            }
        }
    }
}

@media only screen and (max-width: 625px){
    .root{
        .block{
            div{
                justify-content: space-between;
                a{
                    flex-basis: 48%;
                }
            }
        }
    }
}